// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import "babel-polyfill";
import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
import 'assets/custom-theme/index.css'; // element-ui css
import NProgress from 'nprogress'; // Progress 
import 'nprogress/nprogress.css'; // Progress  
import 'normalize.css/normalize.css'; // normalize.css 
import 'assets/iconfont/iconfont'; // iconfont https://github.com/PanJiaChen/vue-element-admin/wiki
import * as filters from './filters'; // vue filter
import Multiselect from 'vue-multiselect'; // element-uiselect
import 'vue-multiselect/dist/vue-multiselect.min.css'; // css
import fontAwesome from 'font-awesome/scss/font-awesome.scss';
import Sticky from 'components/Sticky'; // header
import IconSvg from 'components/Icon-svg'; // svg 
import vueWaves from './directive/waves'; // 
import errLog from 'store/errLog'; // error log
import './mock/index.js'; // mockjs
import $ from 'jquery';

// register globally
Vue.component('multiselect', Multiselect);
Vue.component('Sticky', Sticky);
Vue.component('icon-svg', IconSvg)
Vue.use(ElementUI);
Vue.use(vueWaves);

// register global utility filters.
Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key])
});

// permissiom judge
function hasPermission(roles, permissionRoles) {
    if (roles.indexOf('admin') >= 0) return true; // admin 
    if (!permissionRoles) return true;
    return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

// register global progress.
const whiteList = ['/login', '/regist']; // 
router.beforeEach((to, from, next) => {
    NProgress.start(); // Progress
    const token = store.getters.token;
    if (token) { // token
        if (to.path === '/login') {
            next({ path: '/' });
        } else {
            if (store.getters.roles.length === 0 ) { // user_info
                store.dispatch('GetInfo').then(res => { // user_info
                    //  admin
                    //const roles = [res.name];
                    const roles = "admin";
                    store.dispatch('GenerateRoutes', { roles }).then(() => { // 
                        router.addRoutes(store.getters.addRouters) // 
                        next({ ...to }); // hack addRoutes
                    })
                }).catch(error=>{

                });
            } else {
                // next()  
                if (hasPermission(store.getters.roles, to.meta.role)) {
                    next(); //
                } else {
                    next({ path: '/401', query: { noGoBack: true } });
                }
                //  
            }
        }
    } else {
        if (whiteList.indexOf(to.path) !== -1) { // 
            next()
        } else {
            next('/login'); // 
            NProgress.done(); // hash hash  afterEach hack pshistory
        }
    }
});

router.afterEach(() => {
    NProgress.done(); // Progress
});

Vue.config.productionTip = false;

// 
if (process.env === 'production') {
    Vue.config.errorHandler = function(err, vm) {
        console.log(err, window.location.href);
        errLog.pushLog({
            err,
            url: window.location.href,
            vm
        })
    };
}

new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
})


Vue.config.devtools = true


